              .dropdown-btn {
                &:hover {
                  border: 1px solid darken($grey_light, 20%);
                }
              }
              .dropdown-panel {
                position: absolute;
                grid-template-columns: 100%;
                z-index: 1;
                top: 100%;
                left: 0;
                right: auto;
                width: 100%;
                height: auto;
                padding: 10px $mobile_side-padding;
                background-color: $white;
                box-shadow: 0px 5px 10px rgba(0,0,0,0.12);
                opacity: 0;
                @include transform(translateY(-10px));
                .dropdown-panel-header {
                  display: none;
                }
                .dropdown-panel-main {
                  grid-column: 1 / span 1;
                  grid-row: 1 / span 1;
                }
              }
              .product-guide-drawer { // general
                .drawer-main {
                  padding-top: 50px;
                }
              }

              // Ring size
              &.customise-ring-size {
                .dropdown-panel {
                  ul.ring-size-options,
                  .ring-size-bespoke {
                    max-width: 400px;
                    margin: 0 auto;
                  }
                  ul.ring-size-options {
                    grid-template-columns: 1fr 1fr 1fr;
                    grid-gap: 0 15px;
                    justify-items: left;
                    padding: 0;
                    max-width: none;
                    li {
                      a.ring-size-option {
                        padding: 12px 0;
                        &:hover {
                          text-decoration: underline;
                        }
                      }
                    }
                  }
                  .ring-size-bespoke {
                    .inner {
                      display: inline-block;
                      width: 100%;
                      max-height: 0;
                    }
                  }
                }
                .product-guide-drawer {
                  .drawer-main {
                    img {
                      max-height: 180px;
                    }
                  }
                }
              } // end .customise-ring-size

              // Birthstone
              &.customise-birthstone {
                .dropdown-panel {
                  ul.birthstone-options {
                    max-width: none;
                    padding: 0;
                    li {
                      a.birthstone-option {
                        padding: 15px 0;
                        padding-left: 40px;
                        overflow: hidden; // prevent shine from overflowing
                        &:after { // shine
                          content: "";
                          position: absolute;
                          top: 7px;
                          left: 0;
                          width: 35px;
                          height: 35px;
                          border-radius: 50%;
                          background: linear-gradient(
                            to bottom,
                            rgba(255, 255, 255, 0),
                            rgba(255, 255, 255, 0.5),
                            rgba(255, 255, 255, 0)
                          );
                          opacity: 0;
                        }
                        &:hover {
                          &:after {
                            @include animation('metalSwitcherShine 0.7s linear both');
                          }
                        }
                      }
                      &.no-birthstone {
                        a.birthstone-option {
                          padding: 15px;
                        }
                      }
                    }
                  }
                }
                .product-guide-drawer {
                  .drawer-main {
                    ul {
                      li {
                        padding-left: 120px;
                        img {
                          width: 100px;
                        }
                      }
                    }
                  }
                }
              } // end .customise-birthstone

              // Starsign, Starsign Symbol, Zodiac
              &.customise-starsign {
                .dropdown-panel {
                  ul.starsign-options {
                    max-width: none;
                    padding: 0;
                    li {
                      a.starsign-option {
                        padding: 12px 0;
                        padding-left: 40px;
                        overflow: hidden; // prevent shine from overflowing
                        &:after { // shine
                          content: "";
                          position: absolute;
                          top: 7px;
                          left: 0;
                          width: 35px;
                          height: 35px;
                          border-radius: 50%;
                          background: linear-gradient(
                            to bottom,
                            rgba(255, 255, 255, 0),
                            rgba(255, 255, 255, 0.5),
                            rgba(255, 255, 255, 0)
                          );
                          opacity: 0;
                        }
                        &:hover {
                          &:after {
                            @include animation('metalSwitcherShine 0.7s linear both');
                          }
                        }
                      }
                    }
                  }
                }
                .product-guide-drawer {
                  .drawer-main {
                    ul {
                      li {
                        padding-left: 120px;
                        img {
                          width: 100px;
                        }
                      }
                    }
                  }
                }
              } // end .customise-starsign

              // Coordinates
              &.customise-coordinates {
                .product-guide-drawer {
                  .drawer-main {
                    img {
                      max-height: 180px;
                    }
                  }
                }
              } // end .customise-coordinates

              // Initials
              &.customise-initials {
                //grid-template-columns: minmax(300px, auto) auto;
                .grid-item {
                  //justify-self: start;
                  align-self: center;
                  > span {
                    max-width: none;
                  }
                  .initials-selection {
                    span {
                      &:hover {
                        border: 1px solid $grey;
                      }
                      &.active {
                        border: 1px solid $grey;
                      }
                    }
                    .pointer {
                      display: block;
                      position: absolute;
                      top: 100%;
                      left: 0;
                      width: 15px;
                      height: 15px;
                      background-repeat: no-repeat;
                      background-size: contain;
                      background-position: bottom center;
                      background-image: url($icon_chevron_dark);
                      @include transform(translateX(14px) translateY(5px) rotate(180deg));
                      opacity: 0;
                      pointer-events: none;
                      @include transition(all 0.2s $ease, left 0s $ease_in_out_quart);
                      &.active {
                        opacity: 0.3;
                        @include transition(all 0.2s $ease, left 0.3s $ease_in_out_quint);
                        @include transform(translateX(14px) translateY(0) rotate(180deg));
                      }
                    }
                  }
                }
                .change-disc {
                  .dropdown-btn {
                    font-size: 14px;
                    line-height: 1.2;
                    //max-width: 70px;
                    white-space: normal;
                    overflow: inherit;
                    text-align: left;
                    &:after {
                      @include transform(none);
                      top: 40%;
                    }
                    &:hover {
                      &:after {
                        @include animation('chevronHintDown 0.4s ease');
                      }
                    }
                  }
                  .dropdown-panel { // change disc panel
                    width: auto;
                    min-width: 300px;
                    left: 50%;
                    @include transform(translateX(-50%) translateY(-10px));
                    right: auto;
                    .dropdown-panel-main {
                      padding-top: 0px;
                      ul {
                        li {
                          a {
                            span {
                              margin-left: 40px;
                            }
                            &:hover {
                              text-decoration: none;
                              background-color: $grey_lightest;
                              span.price {
                                text-decoration: none;
                              }
                            }
                          }
                        }
                      }
                    }
                    &.active {
                      @include transform(translateX(-50%) translateY(0));
                    }
                  }
                }
                > .dropdown-panel { // initial panel
                  top: calc(100% + 14px - 46px); // 100% + space for pointer - height of disc-change area
                  .dropdown-panel-main {
                    padding-bottom: 0px;
                    padding: 10px 0;
                    ul {
                      li {
                        @include transform(scale(1));
                        @include transition(all 0.2s $ease);
                        a {
                          height: 55px;
                          &:after { // shine
                            content: "";
                            position: absolute;
                            top: 0;
                            left: 0;
                            width: 100%;
                            height: 100%;
                            border-radius: 50%;
                            background: linear-gradient(
                              to bottom,
                              rgba(255, 255, 255, 0),
                              rgba(255, 255, 255, 0.5),
                              rgba(255, 255, 255, 0)
                            );
                            opacity: 0;
                          }
                          &:hover {
                            &:after {
                              @include animation('metalSwitcherShine 0.7s linear both');
                            }
                          }
                        }
                        &:active {
                          @include transform(scale(0.85)); // pressed-in state
                        }
                      }
                    }
                  }
                  .dropdown-panel-footer {
                    display: none;
                  }
                }
              } // end .customise-initials

            } // end .customise-section

  //         } // end .product-customise
  //       } // end #product-info
  //     } // end .grid
  //   }
  // } // end #product-top

} // end media-query medium-large











